const zhText = {
    email_empty: "",
    email_invalid: "",
    email_input: "",
    auth_code_empty: "",
    load_def_text: "",
    code_input: "",
    net_error: "",
    net_fail: "",
    send_code_btn_text: "",
    re_send_code_btn_text: "",
    login_btn_text: ""
}

const enText = {
    email_empty: "Empty Email",
    email_invalid: "Email Invalid",
    email_input: "Enter Email",
    auth_code_empty: "Empty Auth Code",
    load_def_text: "Please Waiting",
    code_input: "Enter Auth Code",
    net_error: "NET ERROR",
    send_code_btn_text: "Send Auth Code",
    re_send_code_btn_text: "Send Auth Code Again",
    login_btn_text: "LOGIN",
}

export function getLocalText(code) {
    let language = localStorage.getItem("pxai_sms_lang");
    switch (language) {
        case "zh":
            return zhText[code];
        case "en":
            return enText[code];
        default:
            return zhText[code];
    }
}